import React, { Component } from "react";
import "./Rechtsgebieden.css";
import "../general.css";
let catdoc = require("./categories.json");

class Rechtsgebieden extends Component {

	state = {expanded: false, explantation: ""}

	setExplanation = (e, cat) => {
		e.preventDefault();
		console.log(cat)
		if (this.state.expanded === true && this.state.explantation === cat){
			this.setState({expanded: false, explantation: ""})
		} else{
			this.setState({expanded: true, explantation: cat})
		}

	}

	closeExple = e => {
		e.preventDefault();
		this.setState({expanded: false, explantation: ""})
	}

	render() {
		return(
			<div className="body" id="rechtsgebieden">
				<div className="subjectHeader">
					Rechtsgebieden
				</div>
				<div className="RGcontent">
					<div className="catsAndExpl">
						<div className="cats">
							<div className="category" id={(this.state.explantation === "pf").toString()} onClick={e => this.setExplanation(e, "pf")}>
								Personen- en familierecht
							</div>
							<div className="category" id={(this.state.explantation === "ar").toString()} onClick={e => this.setExplanation(e, "ar")}>
								Arbeidsrecht
							</div>
							<div className="category" id={(this.state.explantation === "sz").toString()}  onClick={e => this.setExplanation(e, "sz")}>
								Sociaal zekerheidsrecht
							</div>
							<div className="category" id={(this.state.explantation === "sv").toString()} onClick={e => this.setExplanation(e, "sv")}>
								Sociale voorzieningen
							</div>
							<div className="category" id={(this.state.explantation === "hr").toString()} onClick={e => this.setExplanation(e, "hr")}>
								Huurrecht
							</div>
							<div className="category" id={(this.state.explantation === "vv").toString()} onClick={e => this.setExplanation(e, "vv")}>
								Vermogens- en verbintenissenrecht
							</div>
							<div className="category" id={(this.state.explantation === "er").toString()} onClick={e => this.setExplanation(e, "er")}>
								Erfrecht
							</div>
						</div>
						{
							this.state.expanded && (this.state.explantation === "ar" || this.state.explantation === "pf") &&
							<div className="expl">
								<h2>Onderwerpen: </h2>
								<Explanation code={this.state.explantation} close={this.closeExple}/>
							</div>
						}
					</div>
				</div>
			</div>
		)
	}
}

class Explanation extends Component {

	render(){
		return(
			<div onClick={this.props.close}>
				{catdoc[this.props.code].map( val => {
					return(
						<p key={val}>{val}</p>
					)
				})}
			</div>
		)
	}
}

export default Rechtsgebieden;