import React, { Component } from "react";
import { Link, animateScroll as scroll } from "react-scroll";
import "./Kantoor.css";
import "../general.css";
let texts = require("./texts.json");

class Kantoor extends Component {
	render() {
		return(
			<div className="body" id="kantoor">
				<div className="intro">
					<div className="kantoorTitle">
						Dorhout Advocatuur
					</div>
					<p className="introText">
						{texts.intro}
					</p>
				</div>
				<div className="kantoorContent">
					<div className="participants">
						<div className="participant">
							<div className="name">
								Mr. G.R. Dorhout - Tielken
							</div>
							<div className="job">
								Advocaat
							</div>
							<div className="partText">
								<p>
									{texts.germaine}
								</p>
								<p>
									{texts.germaine2}
								</p>
							</div>
						</div>
						<div className="participant">
							<div className="name">
								Mr. A.F. Dorhout
							</div>
							<div className="job">
								Juridisch adviseur
							</div>
							<div className="partText">
								<p>
									{texts.toon}
								</p>
							</div>
						</div>
					</div>
				</div>
				<div className="contactButton">
					<Link activeClass="active" to="contact" spy={true} smooth={true} duration={300}>
						<button className="cButton">Contact opnemen</button>
					</Link>
				</div>
			</div>
		)
	}
}

export default Kantoor;