import React, { Component } from "react";
import "./AlgVoorwaarden.css"
import "../general.css";
let vw = require("./algV.json")

class AlgVoorwaarden extends Component {
	render() {
		return(
			<div className="body" id="algVoorwaarden">
				<div className="subjectHeader">
					Algemene voorwaarden
				</div>
				<div className="algVBody">
					<ol className="vwlist">
						{
							vw.map((line, i) => {
								return(
									<li className="alinea" key={i}>{line}</li>
								)
							})
						}
					</ol>
				</div>
			</div>
		)
	}
}

export default AlgVoorwaarden;