import React, { Component } from "react";
import { Link, animateScroll as scroll } from "react-scroll";
import "./header.css";

class Header extends Component {

	render(){
		return(
			<div className="headerBody">
				<div className="logo">
					<Link activeClass="active" to="kantoor" spy={true} smooth={true} duration={300}>Dorhout Advocatuur</Link>
				</div>
				<div className="menuItems">
					<ul className="items">
						<li className="item"><Link activeClass="active" to="kantoor" spy={true} smooth={true} duration={300}>Kantoor</Link></li>
						<li className="item"><Link activeClass="active" to="rechtsgebieden" spy={true} smooth={true} duration={300} offset={-80}>Rechtsgebieden</Link></li>
						<li className="item"><Link activeClass="active" to="kosten" spy={true} smooth={true} duration={300} offset={-80}>Kosten</Link></li>
						<li className="item"><Link activeClass="active" to="algVoorwaarden" spy={true} smooth={true} duration={300} offset={-80}>Algemene voorwaarden</Link></li>
						<li className="item"><Link activeClass="active" to="contact" spy={true} smooth={true} duration={300} offset={-80}>Contact</Link></li>
					</ul>
				</div>
			</div>
		)
	}
}

export default Header;